import { createSlice } from '@reduxjs/toolkit';
import persistReducer from 'redux-persist/es/persistReducer';
import storage from 'redux-persist/lib/storage';

const contactList = [
  { id: 'id-1', name: 'Rosie Simpson', number: '459-12-56' },
  { id: 'id-2', name: 'Hermione Kline', number: '443-89-12' },
  { id: 'id-3', name: 'Eden Clements', number: '645-17-79' },
  { id: 'id-4', name: 'Annie Copeland', number: '227-91-26' },
];
const persistConfig = {
  key: 'contacts',
  storage,
};


const Slice = createSlice({
  name: 'contacts',
  initialState: { value: contactList, filter: '' },
  reducers: {
    addContact(state, action) {
      state.value.push(action.payload);
    },
    deleteContact(state, action) {
      return {
        ...state,
        value: state.value.filter(({ id }) => id !== action.payload),
      };
    },
    setFilter(state, action) {
      return { ...state, filter: action.payload };
    },
  },
});
export const persistedReducer = persistReducer(persistConfig, Slice.reducer);

export const { addContact, deleteContact, setFilter } = Slice.actions;
export default Slice.reducer;
